.delete-link {
  margin-top: 10px; }

.form-control-inline {
  min-width: 0;
  width: auto;
  display: inline; }

.tabs {
  margin-top: 20px; }

.description {
  margin-top: 40px; }

.stars {
  font-size: 22px; }

.rating {
  position: absolute; }

.platform {
  height: 40px;
  margin-right: 15px; }

.review {
  margin-top: 20px; }

.star-selected {
  color: gold !important;
  cursor: pointer; }

.star-inactive {
  color: transparent; }

.star-active {
  color: black; }

.games-list {
  margin-top: 10px; }

.invalid > input, .invalid-select > select, .invalid-textarea > div > .ta-text, .invalid-select > input {
  border-color: red; }

.invalid > label, .invalid-select > label, .invalid-textarea > label {
  color: red; }

.form-invalid > input, .form-invalid > span {
  border-color: red;
  color: red; }

body {
  width: 100%;
  height: 100%; }

.navbar-home {
  font-size: 18px;
  margin-left: -15px; }

.content {
  padding: 0 30px 30px 30px; }

.slider {
  margin-left: auto;
  margin-right: auto;
  width: 70vw; }

.ui-view-container {
  position: relative;
  height: 65px; }

[ui-view].ng-enter, [ui-view].ng-leave {
  position: absolute;
  left: 0;
  right: 0;
  -webkit-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out; }

[ui-view].ng-enter {
  opacity: 0;
  -webkit-transform: scale3d(0.5, 0.5, 0.5);
  -moz-transform: scale3d(0.5, 0.5, 0.5);
  transform: scale3d(0.5, 0.5, 0.5); }

[ui-view].ng-enter-active {
  opacity: 1;
  -webkit-transform: scale3d(1, 1, 1);
  -moz-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1); }

[ui-view].ng-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

[ui-view].ng-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100px, 0, 0);
  -moz-transform: translate3d(100px, 0, 0);
  transform: translate3d(100px, 0, 0); }
